<!--eslint-disable-->
<template>  
  <li class="catalog__item">
    <router-link class="catalog__pic" href="#" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{product.title}}
      </a>
    </h3>

    <span class="catalog__price">
      {{product.price | numberFormat}} ₽
    </span>


    <ul class="colors colors--black">
      <li class="colors__item" v-for="colorForItem in product.colors" :key="colorForItem.id">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="colorForItem.title" v-model="color">
          <span class="colors__value" :style="{backgroundColor: colorForItem.code, border: '1px solid black' }">
          </span>
        </label>
      </li>
      
    </ul>
  </li>
</template>
<!--eslint-disable-->
<script>
import goToPage from '@/helpers/goToPage';
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      color: '#73B6EA'
    };
  },
  props: ['product'],
  filters: {
    numberFormat,
  },
  methods: {
    goToPage,
  },
};
</script>
