<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.product.image"
        width="120"
        height="120"
        srcset="img/phone-square-3@2x.jpg 2x"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">Объем: <span>128GB</span></p>
    <span class="product__code"> Артикул: {{ item.productId }} </span>

    <div class="product__counter form__counter">
      <button type="button"
      id="changeAmount"
      aria-label="Убрать один товар"
      @click.prevent="decreaseAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <!-- eslint-disable-next-line vuejs-accessibility/form-control-has-label -->
      <input type="text" v-model.number="amount" name="count" onclick="this.select()"/>

      <button type="button"
      aria-label="Добавить один товар"
      id="changeAmount"
      @click.prevent="increaseAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.product.price * item.amount) | numberFormat }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions, mapMutations } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId, amount: value,
        });
      },
    },
  },
  methods: {
    ...mapMutations({
      // deleteProduct: 'deleteCartProduct',
      // increaseAmount: 'increaseCartProductAmount',
      // decreaseAmount: 'decreaseCartProductAmount',
    }),
    ...mapActions(['deleteCartProduct', 'increaseCartProductAmount', 'decreaseCartProductAmount']),
    deleteProduct() {
      this.deleteCartProduct({ productId: this.item.productId });
    },
    increaseAmount() {
      this.increaseCartProductAmount({ productId: this.item.productId, amount: this.item.amount });
    },
    decreaseAmount() {
      this.decreaseCartProductAmount({ productId: this.item.productId, amount: this.item.amount });
    },
  },
};
</script>

<style>
#changeAmount:active {
  transform: scale(1.7,1.7);
}
</style>
